"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Sun, Moon, Facebook, Instagram } from "react-feather";
import Link from "next/link";

export default function Layout({ children }) {
  const [isDarkMode, setIsDarkMode] = useState(false);

  useEffect(() => {
    const savedTheme = localStorage.getItem("theme");
    if (savedTheme) {
      setIsDarkMode(savedTheme === "dark");
      document.documentElement.classList.toggle("dark", savedTheme === "dark");
    }
  }, []);

  const toggleDarkMode = () => {
    setIsDarkMode(!isDarkMode);
    const newTheme = !isDarkMode ? "dark" : "light";
    localStorage.setItem("theme", newTheme);
    document.documentElement.classList.toggle("dark", newTheme === "dark");
  };

  return (
    <div className={`flex flex-col min-h-screen ${isDarkMode ? "dark" : ""}`}>
      <header className="flex justify-between p-4 bg-gray-100 dark:bg-gray-900">
        <Button
          variant="ghost"
          className="flex items-center space-x-2"
          onClick={toggleDarkMode}
        >
          {isDarkMode ? <Sun size={20} /> : <Moon size={20} />}
          <span>{isDarkMode ? "Light Mode" : "Dark Mode"}</span>
        </Button>
        <div className="flex space-x-4">
          <Link href="https://www.facebook.com/denimshack" target="_blank">
            <Button variant="ghost" className="flex items-center space-x-2">
              <Facebook size={20} />
            </Button>
          </Link>
          <Link href="https://www.instagram.com/denimshack" target="_blank">
            <Button variant="ghost" className="flex items-center space-x-2">
              <Instagram size={20} />
            </Button>
          </Link>
        </div>
      </header>
      <main className="flex-grow">{children}</main>
      <footer className="p-4 bg-gray-100 dark:bg-gray-900 text-center">
        <p className="text-gray-700 dark:text-gray-300">&copy; 2025 Denim Shack. All rights reserved.</p>
      </footer>
    </div>
  );
}